<template>
  <div class="flex-row" v-loading="loading">
    <sidebar 
    v-if="isAuthor"
    :tabs="tabs"
    :active="active"
    @click="toClickTab"
    />
    <div class="flex-1">
      <general 
      v-if="active === 0"
      :data="data"
      @comment="toComment"
      @vote="toVote"
      />
      <cards 
      v-else-if="active === 1"
      :data="data.cards"
      />
      <settings 
      v-else-if="active === 2"
      :data="data.settings"
      @del="toDel"
      @update="toUpdate"
      />
    </div>
  </div>
</template>

<script>
import General from './components/general.vue';
import Cards from './components/cards.vue';
import Settings from './components/settings.vue';

export default {
  components: { 
    General,
    Cards,
    Settings,
  },
  props: {
    title: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      loading: true,

      data: {},

      active: -1,
      tabs: [
        {
          name: '基础信息',
          component: 'general',
        },
        {
          name: '卡片',
          component: 'cards',
        }, 
        {
          name: '设置',
          component: 'settings',
        }
      ],
      isAuthor: false,

      settings: {
        flag: false,
      }
    }
  },
  mounted() {
    this.$axios.get(`${this.$url.cards}/${this.title}`)
    .then((resp) => {
      this.data = resp.data;
      this.data.stars.forEach((obj) => {
        if (obj.username === window.$user.username) {
          this.data.vote.disable = true;
          this.data.vote.count = obj.count ;
        }
      })
      if (resp.data.isAuthor) {
        this.isAuthor = true;
      }
      this.active = 0;
      this.loading = false;
    })
  },
  methods: {
    toClickTab(index) {
      this.active = index;
    },
    toComment(data) {
      this.data.comments.push(data)
      this.$axios.post(`${this.$url.comments.cards}/${this.title}`, data)
      .then(() => {})
    },
    toVote(count) {
      if (!window.$user) {
        this.$alert('error', '請先登錄');
        return;
      }
      this.data.vote.disable = true;
      this.data.vote.count = count;
      this.$axios.put(`${this.$url.cards}/${this.data.title}/stars`, { count })
        .then((resp) => {
        })
    },
    toUpdate(settings) {
      if (this.settings.flag) {
        return;
      }
      this.$axios.put(`${this.$url.cards}/${this.title}/settings`, { settings })
        .then((resp) => {
          this.$alert('success', '更新成功');
          this.settings.flag = false;
        })
    },
    toDel() {
      this.$axios.delete(`${this.$url.cards}/${this.title}`)
      .then((resp) => {
        if (resp.data.ok) {
          this.$alert('success', '删除成功!');
          setTimeout(() => {
            this.$router.replace('/');
          }, 300);
        }
      })
    }
  }
}
</script>