<template>
  <form class="pd-1 h-100 flex-column pure-form pure-form-stacked w-50">
    <span>访问限制</span>
    <label for="public">
      <input type="radio" name="public" id="public" :value="0" v-model="settings.accessbility">
      公开
    </label>
    <label for="private">
      <input type="radio" name="private" id="private" :value="1" v-model="settings.accessbility">
      隐藏
    </label>
    <div class="mt-auto flex-row">
      <button class="ml-auto pure-button pure-button-primary" @click.prevent="toSave">保存</button>
      <button class="ml-1 pure-button button-error" @click.prevent="toDel">删除</button>
    </div>
  </form>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
      required: true,
    }
  },
  data() {
    return {
      settings: this.deepCopy(this.data),
    }
  },
  methods: {
    toSave() {
      this.$emit('update', this.settings);
    },
    toDel() {
      this.$emit('del');
    }
  }
}
</script>
