<template>
    <div class="card-group pd-1 flex-column">
      <div class="flex-row">
        <h1>{{ data.title }}</h1>
        <button 
          class="
          pure-button pure-button-primary ml-auto
          flex-item-center
          ">
          <a :href="'/practising/' + data.title" target="_blank">
            开始练习
          </a>
        </button>
      </div>
      <div class="flex-row">
        <img
          src="/logo.png" alt="cover"
          width="200px"
          height="200px"
          style="object-fit: cover;"
        >
        <ul class="ml-1 flex-1 meta-list">
          <li class="meta-list-item">
            <span class="meta-key">作者</span>
            <span class="meta-value">{{ data.author ? data.author : '' }}</span>
          </li>
          <li class="meta-list-item">
            <span class="meta-key">發佈日期</span>
            <span class="meta-value">{{ (data.createdAt ? data.createdAt : 0) | formatDateTime }}</span>
          </li>
          <li class="meta-list-item">
            <span class="meta-key">目錄</span>
            <span class="meta-value">
              {{ data.category ? data.category.toString() : '' }}
            </span>
          </li>
          <li class="meta-list-item">
            <span class="meta-key">评价</span>
            <star
              class="meta-value"
              :disable="data.vote ? data.vote.disable : false"
              :count="6 - (data.vote ? data.vote.count : 0)"
              @click="toVote"
            />
          </li>
          <li class="meta-list-item">
            <span class="meta-key">標簽</span>
            <tag 
            class="meta-value"
            v-for="tag in data.tags"
            :text="tag"
            :key="tag"
            />
          </li>
          <li class="meta-list-item">
            <span class="meta-key">卡片數量</span>
            <span class="meta-value">{{ data.cards ? data.cards.length : 0 }}</span>
          </li>
        </ul>
        <rating 
        :data="data.rating"
        />
      </div>
      <div class="mt-1">
        {{ data.intro ?  data.intro : '' }}
      </div>
      <my-comment 
      class="mt-1"
      :data="data.comments ? data.comments : []"
      @submit="toComment"
      />
      </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
      required: true,
    }
  },
  methods: {
    toComment(data) {
      this.$emit('comment', data);
    },
    toVote(count) {
      this.$emit('vote', count);
    }
  }
}
</script>

<style lang="scss" scoped>
.card-group {
  width: 600px;
  margin: 0 auto;
}

.meta-list-item {
  margin-top: 0.4em;
}

.meta-key {
  display: inline-block;
  position: relative;
  vertical-align: top;
  text-align: left;
  color: #777;
  font-size: 0.8em;
  &::after {
    content: ":";
  }
}
.meta-value {
  display: inline-block;
  vertical-align: top;
  margin-left: 0.4em;
  font-size: 0.8em;
  color: #555;
}
</style>
